
#app .my-app-nav {
  background-color: #00d1b2;
  border-bottom: 1px solid rgba(255,255,255,0.5);
}
#app .my-app-nav .navbar {
  background-color: #00d1b2;
}
#app .my-app-nav .navbar-item img {
  max-height: 36px;
}
#app .my-app-nav .navbar-item > a,
#app .my-app-nav .navbar-item > .navbar-link {
  color: #fff;
}
#app .my-app-nav .navbar-item > .navbar-link::after {
  border-color: #fff;
}
#app .my-app-nav .navbar-item:hover > a,
#app .my-app-nav .navbar-item:hover > .navbar-link {
  color: #0a0a0a;
}
#app .my-app-nav .navbar-item:hover > .navbar-link::after {
  border-color: #00d1b2;
}


